/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton__negro {
  background-color: #000000;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin: 1rem;
  text-align: center;
  margin-top: calc(5rem / 2);
  border: none;
}
.boton__negro:hover {
  background-color: #1a1a1a;
  cursor: pointer;
}

.boton__negro-inline {
  background-color: #000000;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin: 1rem;
  text-align: center;
  margin-top: calc(5rem / 2);
  border: none;
}
.boton__negro-inline:hover {
  background-color: #1a1a1a;
  cursor: pointer;
}

.boton__display {
  display: inline-block;
}

.boton__vinotinto {
  background-color: #b10b0e;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin: 1rem;
  text-align: center;
  margin-top: calc(5rem / 2);
  border: none;
}
.boton__vinotinto:hover {
  background-color: #e10e12;
  cursor: pointer;
}

.boton__vinotinto-inline {
  background-color: #b10b0e;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin: 1rem;
  text-align: center;
  margin-top: calc(5rem / 2);
  border: none;
}
.boton__vinotinto-inline:hover {
  background-color: #e10e12;
  cursor: pointer;
}

.boton__rojo {
  background-color: #d3070f;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin: 1rem;
  text-align: center;
  margin-top: calc(5rem / 2);
  border: none;
}
.boton__rojo:hover {
  background-color: #f7161f;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #d3070f;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin: 1rem;
  text-align: center;
  margin-top: calc(5rem / 2);
  border: none;
  width: 92%;
}
.boton-rojo-block:hover {
  background-color: #f7161f;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin: 1rem;
  text-align: center;
  margin-top: calc(5rem / 2);
  border: none;
}
.boton-verde-block:hover {
  background-color: #92e400;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin: 1rem;
  text-align: center;
  margin-top: calc(5rem / 2);
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #f6a026;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode h5, .dark-mode h6 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .imagen-contacto .fondo-Contacto {
  background-color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}
.dark-mode .formulario option {
  color: #000000;
}
.dark-mode .formulario p, .dark-mode .formulario legend, .dark-mode .formulario label {
  color: #e1e1e1;
}
.dark-mode .formulario .boton__negro:hover {
  background-color: #e1e1e1;
  color: #000000;
  transition: background-color 0.5s, color 0.5s;
}
.dark-mode td {
  color: #FFFFFF;
}

/** UI **/
.no.webp .inicio {
  background-image: url(../img/header.jpg);
  background-size: cover;
  height: 10px;
}

.webp .inicio {
  background-image: url(../img/header.webp);
  background-size: cover;
  height: 10px;
}

.header__imagen {
  position: relative;
  height: 50rem;
}
.header__imagen img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header {
  background-color: #b10b0e;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  margin: 0 auto;
  padding: 1rem 0;
  background-color: #b10b0e;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-bottom: 2rem;
  padding-top: 2rem;
  text-align: center;
}
.footer .contenedor-footer .navegacion {
  overflow: visible;
  display: flex;
  font-size: 1rem;
  justify-content: center;
  padding-bottom: 3rem;
  gap: 1rem;
}
@media (min-width: 768px) {
  .footer .contenedor-footer .navegacion {
    gap: 1rem;
    font-size: x-large 2px;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-bottom: 1.5rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .barra {
    align-items: flex-end;
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    font-size: 1.8rem;
    opacity: 1;
    overflow: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion {
    gap: 2rem;
  }
}
.navegacion :hover {
  color: red;
}

.mostrar {
  max-height: 50rem;
}

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
  filter: hue-rotate(0deg);
}
.mobile-menu:hover {
  filter: hue-rotate(45deg);
}

.animacion {
  transition: src 0.3s ease-in-out;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #e1e1e1;
  border: 1px solid #e1e1e1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.anuncio .contenido-anuncio {
  gap: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio img {
  width: 100%;
  height: 30rem;
}
.anuncio .texto-limitado {
  white-space: nowrap; /* Evita el salto de l√≠nea */
  overflow: hidden; /* Oculta el contenido adicional */
  text-overflow: ellipsis; /* Muestra los tres puntos (...) al final del texto cortado */
  max-width: 350px; /* El ancho m√°ximo que deseas establecer */
}

.precio {
  color: #71B100;
  font-weight: bold;
  font-size: 3rem;
  text-align: center;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  text-align: center;
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto .fondo-Contacto {
  background-color: #e1e1e1;
  border-radius: 2rem;
  padding: 2rem;
}
.imagen-contacto .fondo-Contacto a {
  text-align: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: bold;
  color: #000000;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #000000;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
  gap: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    gap: 2;
    grid-template-columns: repeat(2, 1fr);
  }
  .entrada-blog:last-of-type {
    margin-bottom: 0;
  }
}

.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
  margin-top: 10rem;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #b10b0e;
  margin-top: 1rem;
}

.informacion__blog span {
  font-weight: bold;
  color: #b10b0e;
}

.testimoniales {
  background-color: #b10b0e;
  color: #FFFFFF;
  font-size: 2.4rem;
}
.testimoniales blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimoniales blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimoniales p {
  color: #FFFFFF;
  font-weight: bold;
  text-align: right;
  margin: 2rem;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  display: block;
  text-transform: uppercase;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select, .formulario option {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #e1e1e1;
  border: 1px solid #e1e1e1;
  border-radius: 2rem;
}
.formulario textarea {
  height: 20rem;
  resize: none;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario select::-ms-expand {
  display: none;
}
.formulario select:hover::-ms-expand {
  background-color: transparent;
}

.contenido-nosotros {
  padding: 1rem;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-nosotros h3 {
  text-align: left;
  font-weight: bold;
}
.contenido-nosotros h3 p {
  font-weight: 400;
  text-align: justify;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #d3070f;
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 20rem;
}