.formulario{
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0 ;
    }
    legend{
        font-size: 2rem;
        color: $grisOscuro;
    }
    label{
        font-weight: $bold;
        display: block;
        text-transform: uppercase;
    }
    input:not([type="submit"]),textarea,select,option{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $gris;
        border: 1px solid $gris;
        border-radius: 2rem;
    }
  

    textarea{
        height: 20rem;
        resize:none
    }
    .forma-contacto{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    input[type="radio"]{
        margin:0;
        width: auto;
    }

    select {
        // Estilos generales del select
      
        &::-ms-expand {
          display: none;
        }
      
        &:hover::-ms-expand {
          background-color: transparent;
        }
      }

}